<template>
  <div class="TaskDetails" v-if="bountyDetail">
    <div class="banner"></div>
    <div class="content_bg">
      <div class="content">
        <div class="titleDate">
          <div class="title">{{ $t("index.switch") === "en" ? bountyDetail.titleEn : bountyDetail.title }}</div>
          <div class="date">
            {{ $t("task.Completion") }} ：<span class="num">{{ bountyDetail.reward }}</span>UENC
          </div>
        </div>
        <div class="text">{{ $t("index.switch") === "en" ? bountyDetail.descriptionEn : bountyDetail.description }}</div>
        <div class="num"><i class="el-icon-circle-check"></i>{{ $t("task.Number") }} ：{{ bountyDetail.applyCount }}</div>
        <div class="githup">
          <i class="el-icon-circle-check"></i>Github ：<a target="_blank" :href="bountyDetail.github">{{ bountyDetail.github }}</a
          >{{ $t("task.Click") }}
        </div>
        <div class="timeSubmit">
          <div class="time">
            <i class="el-icon-circle-check"></i>{{ $t("task.Deadline") }} ：<span>{{ bountyDetail.endTime }}</span>
          </div>
          <div class="submit">
            <el-button
             v-if="bountyDetail.isApply === '2'"
             type="info"  size="small" disabled>
              {{ $t("index.switch") === "cn" ? '已报名': 'Have to sign up' }}
            </el-button>
            <el-button
             v-else-if="bountyDetail.isApply === '3'"
             type="info"  size="small" disabled>
              {{ $t("index.switch") === "cn" ? '任务已结束': 'Mission over' }}
            </el-button>
            <el-button  v-else style="background: #5432F4;" type="primary" size="small" @click="apply">{{ $t("task.task") }}</el-button>
          </div>
        </div>
      </div>
    </div>

    <el-dialog
      :title="$t('task.apply')"
      :visible.sync="dialogVisible"
      width="86%"
      :before-close="handleClose"
      :show-close="false"
    >
      <span class="prompt">{{ $t("task.info") }}</span>
      <el-form :model="applyForm" class="demo-form-inline">
        <el-form-item :label="$t('dialog.id')">
          <el-input v-model="applyForm.facebook"></el-input>
        </el-form-item>
        <el-form-item :label="$t('dialog.github')">
          <el-input v-model="applyForm.github"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose" size="small">{{ $t("dialog.cancel") }}</el-button>
        <el-button type="primary" @click="submit" size="small">{{
          $t("dialog.confirm")
        }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
class ApplyForm {
  constructor() {
    this.facebook = "";
    this.github = "";
  }
}
import { GetBountyDetail, GetBountyApply } from "@/assets/server/api.js";
export default {
  data() {
    return {
      dialogVisible: false,
      bountyDetail: null,
      applyForm: new ApplyForm()
    };
  },
  created() {
    this.getBountyDetail();
  },
  methods: {
    async getBountyDetail() {
      this.id = this.$route.query.id;
      this.loginName = localStorage.getItem("username") || null;
      const data = await GetBountyDetail(this.id + "/" + this.loginName);
      if (data.code === 0) {
        this.bountyDetail = data.data;
      } else {
        this.$notify({ type: 'danger', message: this.$t("index.switch") === "cn" ?"接口错误！":'Interface error!'})
      }
    },
    handleClose() {
      this.dialogVisible = false;
      this.applyForm = new ApplyForm();
    },
    apply() {
      this.getBountyDetail();
      if (this.loginName) {
        this.dialogVisible = true;
        this.applyForm.bountyTaskId = this.bountyDetail.taskId;
      } else {
        this.$notify({ type: 'danger', message: this.$t("index.switch") === "cn" ?"请先登录再报名" : "Please sign in first"})
      }
    },
    async submit() {
      let loginName = localStorage.getItem("username");
      if (!loginName) this.$notify({ type: 'danger', message: this.$t("index.switch") === "cn" ?"请先去登录或注册":'Please login or register'}) 
      this.applyForm.loginName = loginName
      for (const key in this.applyForm) {
        const element = this.applyForm[key];
        if (element === "" || !element) {
          return this.$notify({ type: 'danger', message: this.$t("index.switch") === "cn" ?"不能为空":'Can it be empty'}) 
        }
      }
      const data = await GetBountyApply({ ...this.applyForm });
      if (data.code === 0) {
         this.$notify({ type: 'success', message: this.$t("index.switch") === "cn" ?"报名成功！":'success'}) 
        this.handleClose();
        this.getBountyDetail();
      } else {
        this.$notify({ type: "error", message: data.msg });
      }
    }
  }
};
</script>

